.tb_ss_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.tb_ss_post_wrapper, .tb_ss_slider_wrappper, .tb_ss_slider_wrappper .splide__track, .tb_ss_slider_wrappper .splide__slide{
    width: 100%;
    height: 100%;
}

.tb_ss_post_in{
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: end;
    max-width: 100%;
    height: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, .20);
}
.tb_ss_contant_wrap_in {
    width: 100%;
    padding: 0 calc(2vh + 2vw);
    position: relative;
    z-index: 1;
}
.tb_ss_post_media_wrapp, .tb_ss_contant_wrapper {
    max-width: 50%;
    flex: 0 0 auto;
    width: 50%;
}

.tb_ss_text_article .tb_ss_contant_wrapper,
.tb_ss_media_article .tb_ss_post_media_wrapp{
    max-width: 100%;
    flex: 1 0 auto;
    width: 100%;
}

.tb_ss_media_wrap{
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tb_ss_blur_bg, .tb_ss_blur_bg_inner{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 0;
}

.tb_css_t .tb_ss_contant_wrapper{
    background-color: var(--tb-bg-color);
}

.tb_ss_blur_bg:before, .tb_ss_blur_bg_inner:before{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.tb_ss_blur_bg:before{
    background: rgba(0,0,0,.5);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
}

.tb_ss_blur_bg_inner:before{
    background: rgba(0,0,0,.3);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(10px);
}

.tb_ss_bg_color{
   // background-image: url('data:image/png;base64,'), linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%);
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%);
}

.tb_ss_post_article{
    position: relative;
    z-index: 1;
    width: 85%;
    margin: 0 auto;
    padding: 2% 0;
    height: 100%;
    height: var(--tb-theme-height);
}

.tb_ss_post_article_full{
    position: relative;
    z-index: 1;
    width: 100%;
    margin: 0 auto;
    padding: 0;
    height: var(--tb-theme-height);
}

.tb_ss_image_wrap_,
.tb_ss_video_wrap_ {
    position: relative;
    padding-bottom: calc(var(--tb-theme-height) - 9.4%);
    background-color: #000;
    width: 100%;
}

.tb_ss_post_article_full .tb_ss_image_wrap_,
.tb_ss_post_article_full .tb_ss_video_wrap_{
    padding-bottom: var(--tb-theme-height);
}

.tb_ss_image, .tb_ss_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_ss_video {
    background-color: #000;
}

.tb_ss_video video,
.tb_ss_video iframe {
    object-fit: cover;
    object-position: center;
}

.tb_ss_video>div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_ss_image_wrap_ img,
.tb_ss_video_wrap_ video,
.tb_ss_video_wrap_ iframe {
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.tb_ss_image{
    transition: 8s all ease-in-out;
}

.is-visible .tb_ss_post_animated_media .tb_ss_image{
    animation: tb_ss_zoom_animation 8s ease-in-out 2s forwards;
}

@keyframes tb_ss_zoom_animation{
    0%{
        transform: scale(1);
    }
    100%{
        transform: scale(1.45);
    }
}

.tb_ss_author_wrapper {
    margin-bottom: 10px;
}

.tb_ss_author {
    width: 100%;
}

.tb_ss_author_profile {
    height: 75px;
    width: 75px;
    border: 3px solid #fff;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    margin-bottom: 15px;
    overflow: hidden;
    background-color: rgba(0,0,0,.2);
}

.tb_ss_author_profile__{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_ss_authorname {
    font-size: calc(12px + .3vw + .3vh);
    font-weight:  var(--tb-author-font-weight);
    margin-bottom: calc(4px + .2vw + .2vh);
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}

.tb_ss_authorname, .tb_ss_username, .tb_ss_time{
    color: var(--tb-author-color);
    font-family: var(--tb-author-font);
    font-style:  var(--tb-author-font-style);
}

.tb_ss_seprator{
    background-color: var(--tb-author-color);
}

.tb_ss_content,
.tb_ss_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_ss_author {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.tb_ss_author_wrapper {
    display: flex;
    align-items: center;
    width: 100%;
}

.tb_ss_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_ss_post_info {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_ss_seprator {
    height: 6px;
    width: 6px;
    min-width: 6px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_ss_username, .tb_ss_time {
    font-size: calc(8px + .18vw + .18vh);
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.tb_ss_username{
    max-width: 65%;
}

.tb_ss_time{
    max-width: 35%;
}

.tb_ss_social_ {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: calc(3vh + 1vw);
}

.tb_ss_social_ico{
    font-size: calc(12px + 1.2vh + 1.2vw);
    color: var(--tb-icon-color);
}

.tb_ss_contant_wrapper {
    background-color: var(--tb-bg-color);
    display: flex;
    align-items: center;
    justify-content: center;
    // flex-direction: column;
    padding: 10px;
    width: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
    
}

.tb_mss_t .tb_ss_contant_wrapper {
    background-color: rgba(0, 0, 0, .5);
}

.tb_ss_content, .tb_ss_rating_content {
    font-size: calc(12px + .8vw);
    text-align: center;
    line-height: 1.6;
    margin-bottom: calc(3vh + 1vw);
    min-height: calc(3vh + 1vw);
    word-break: break-word;
    color: var(--tb-color);
    display: -webkit-box;   
    -webkit-box-orient: vertical;     
    overflow: hidden; 
}

.tb_ss_content{
    -webkit-line-clamp: 6;
}

.tb_ss_content .tb_text_links_, .tb_ss_rating_content .tb_text_links_ {
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_ss_rating_content, .tb_ss_text_post {
    text-align: center;
}

.tb_ss_text_post{
    margin-top: 30px;
    margin-bottom: 20px;
}

.tb_ss_text_decoration::before{
    content: "\e909";
    font-family: 'WebSocialFonts' !important;
    margin-right: 12px;
}

.tb_ss_text_decoration::after{
    content: "\e90a";
    font-family: 'WebSocialFonts' !important;
    margin-left: 12px;
}



.tb_ss_post_article_full .tb_ss_contant_wrapper{
    padding: 0;
}
@media screen and (max-width: 1400px) {
    .tb_ss_author_profile {
        height: 65px;
        width: 65px;
    }
}
@media screen and (max-width: 1200px) {
.tb_ss_author_profile {
    height: 55px;
    width: 55px;
}
.tb_ss_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px
}

.tb_ss_author_profile {
    border: 2px solid #fff;
}
}

@media (orientation: portrait) {
.tb_ss_post_in {
    flex-direction: column;
}
.tb_ss_post_article {
    width: 96%;
}
.tb_ss_post_article_full{
    width: 100%;
}

.tb_ss_image_wrap_,
.tb_ss_video_wrap_ {
    padding-bottom: calc((var(--tb-theme-height) / 2) - 2vw);
}

.tb_ss_media_article .tb_ss_image_wrap_,
.tb_ss_media_article .tb_ss_video_wrap_ {
    padding-bottom: calc(var(--tb-theme-height) - 2vw);
}

.tb_ss_contant_wrapper,
.tb_ss_post_media_wrapp {
    height: calc((var(--tb-theme-height) / 2) - 2vw);
    max-width: 100%;
    width: 100%;
}

.tb_ss_post_article_full .tb_ss_image_wrap_,
.tb_ss_post_article_full .tb_ss_video_wrap_{
    padding-bottom: 50vh;
}

.tb_ss_post_article_full .tb_ss_contant_wrapper,
.tb_ss_post_article_full .tb_ss_post_media_wrapp {
    height: 50vh;
}

.tb_ss_content, .tb_ss_rating_content {
    font-size: calc(12px + .5vw + .5vh);
}
}

.tb_ss_author_wrapper, .tb_ss_social_, .tb_ss_content, .tb_ss_author_profile{
    position: relative;
}

.is-visible .tb_ss_author_wrapper, .is-visible .tb_ss_social_, .is-visible .tb_ss_content, .is-visible .tb_ss_author_profile{
    animation: tb_ss_text_animation 1s 1 cubic-bezier(.165,.84,.44,1) .1s;
    animation-fill-mode: both;
    opacity: 1;
    filter: alpha(opacity=100);
    -webkit-transform: translateZ(0);
}

.is-visible .tb_ss_content{
    animation-delay: .3s;
    -webkit-animation-delay: .3s;
}

.is-visible .tb_ss_author_wrapper{
    animation-delay: .6s;
    -webkit-animation-delay: .6s;
}



@-webkit-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@-moz-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@-o-keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}
@keyframes tb_ss_text_animation{
    0%{
        top:70px;
        opacity:0
    }
    100%,50%{
        top:0;
        opacity:1
    }
}



